import React, { useRef } from "react";
import logo from "./logo.svg";
import "./App.css";
import Webcam from "react-webcam";
import * as faceapi from "face-api.js"
import { TinyFaceDetectorOptions, detectAllFaces } from 'face-api.js';

function App() {
  const webcamRef = useRef(null);
  const canvasRef = useRef(null);

  // OPEN YOU FACE WEBCAM
  const startVideo = async () => {
    await loadModels();
  }
  // LOAD MODELS FROM FACE API

  const loadModels = () => {
    Promise.all([
      // THIS FOR FACE DETECT AND LOAD FROM YOU PUBLIC/MODELS DIRECTORY
      
      faceapi.nets.tinyFaceDetector.loadFromUri("/models"),
      faceapi.nets.faceLandmark68Net.loadFromUri("/models"),
      faceapi.nets.faceRecognitionNet.loadFromUri("/models"),
      faceapi.nets.faceExpressionNet.loadFromUri("/models"),
      faceapi.nets.ageGenderNet.loadFromUri("/models")
    ]).then(() => {
      console.log("models are loaded!")
      faceMyDetect()
    })
  }

  const faceMyDetect = () => {
    setInterval(async () => {
      if (
        typeof webcamRef.current !== "undefined" &&
        webcamRef.current !== null &&
        webcamRef.current.video.readyState === 4) {
        let video = webcamRef.current.video;
        let canvas = document.getElementById("canvas");
        const detections = await detectAllFaces(video, new TinyFaceDetectorOptions).withFaceLandmarks().withFaceExpressions().withAgeAndGender()
        console.log(detections)


        /*
      // DRAW YOU FACE IN WEBCAM
      canvas.innerHtml = faceapi.createCanvasFromMedia(video)
      faceapi.matchDimensions(canvas, {
        width: 940,
        height: 650
      })


      const resized = faceapi.resizeResults(detections, {
        width: 940,
        height: 650
      })

      faceapi.draw.drawDetections(canvas, resized)
      faceapi.draw.drawFaceLandmarks(canvas, resized)
      faceapi.draw.drawFaceExpressions(canvas, resized)*/

      }




    }, 1000)
  }

  // LOAD

  startVideo();





  return (
    <>
      <Webcam ref={webcamRef} style={
        {
          position: "absolute",
          width: "800px",
          height: "600px",
          left: 0,
          right: 0
        }
      } />
      <canvas ref={canvasRef} style={
        {
          position: "absolute",
          width: "800px",
          height: "600px",
          left: 0,
          right: 0
        }
      }></canvas>

    </>
  )

}

export default App;